import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Home from './components/Home';
import Footer from './components/Footer';
import Projects from './components/Projects';
import ProjectDetail from './components/ProjectDetail';
import Collages from './components/Collages'
import Blog from './components/Blog'
import About from './components/About'

import logo from './logo.svg';
import './App.css';

function Users() {
  return <h2>Users</h2>;
}

function getNavBar() {
    return (
      <ul>
        <li  className={window.location.pathname === '/' ? "home-hover" : null }>
          <Link to="/">HOME</Link>
        </li>
        <li  className={window.location.pathname === '/' ? "home-hover" : null }>
          <Link to="/projects">PROJECTS</Link>
        </li>
        <li  className={window.location.pathname === '/' ? "home-hover" : null }>
          <Link to="/collages">COLLAGES</Link>
        </li>
        <li  className={window.location.pathname === '/' ? "home-hover" : null }>
          <Link to="/blogs">BLOGS</Link>
        </li>
        <li  className={window.location.pathname === '/' ? "home-hover" : null }>
          <Link to="/about">ABOUT</Link>
        </li>
      </ul>
    )
}

function App() {
  return (
    <div className="App">
      <Router>
      <div style={{height: "100%"}}>
        <nav  className="app-nav">
            {getNavBar()}

          <div className="icons-container">
            <a className="link-icon email-icon" href="mailto:kristen7an@gmail.com?subject=HI"></a>
            <a  className="link-icon behance-icon" href="https://www.behance.net/KRISTEN7AN7873"></a>
            <a className="link-icon linkedin-icon" href="https://www.linkedin.com/in/kristen-an-9026b2105/"></a>
          </div>
        </nav>

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/projects/:id">
            <ProjectDetail />
          </Route>
          <Route path="/projects">
            <Projects />
          </Route>
          <Route path="/collages">
            <Collages />
          </Route>
          <Route path="/blogs">
            <Blog />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
    <Footer/>
    </div>
  );
}

export default App;
