import React from 'react';
import SimpleImageSlider from "react-simple-image-slider";

import './projectDetail.css'

const project1Len = 3;
const project2Len = 5;

class ProjectDetail extends React.Component {
  // componentDidMount() {
  //   var btns = document.querySelectorAll('.project-detail button')
  //   btns[0].style.left = '-30px';
  //   btns[1].style.right = '-30px';
  // }
  render() {
    const paths = window.location.pathname.split('/')
    const id = paths[paths.length - 1];

    const len = id == 1 ? project1Len : project2Len;

    const imgs = [];
    for (let index = 0; index < len; index++) {
      imgs.push({ url: `/project${id}/${index+1}.jpg`});
    }

    return (<div className="project-detail">
            <div className="sub-title">KRISTEN AN</div>
      <SimpleImageSlider
        width={1000}
        height={1250}
        images={imgs}
        style={{ margin: '0 auto', padding: 60}}
        bgColor='#f7f7f7'
        showBullets={false}
        navStyle={2}
      />
    </div>)
  }
}

export default ProjectDetail;